* {
  box-sizing: border-box;
}

.page {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 10px;
  font-weight: 400;
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  margin: 0 auto;
  padding: 32px 16px 16px;
  display: flex;
}

.card__photo {
  width: 160px;
  height: 134px;
}

.card__name {
  color: #060b35;
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  margin-bottom: 0;
  line-height: 14px;
}

.card__reviews {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 16px;
  margin-top: 16px;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin-top: 24px;
  line-height: 18px;
  display: flex;
}

.card__price-container__-label {
  color: #616070;
  font-size: 12px;
}

.card__price-container .card__price {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #0000;
  border: 1px solid #00acdc;
}

.stars {
  flex-direction: row;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

